import React from "react";
import "./postWord.css" ;
import AudioRecording  from "../audioRecording/audioRecording"
class PostWord extends React.Component {
  constructor(props) {

    super(props);

    this.state = {
      word: "",
      definition: "",
      audioRecorded :[]
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  handleChange (e) {
    this.setState({ [e.target.name] : e.target.value});
 }

 handleSubmit(e){
   alert("started =) !")
   e.preventDefault();

  var wordPost = this.state.word
 var definitionPost = this.state.definition
 var audioRecorded = this.state.audioRecorded
   fetch('http://localhost:7500/api/words', {
     method:'POST',
     headers: {
       'Accept': 'application/json, text/plain, */*',
       'Content-type':'application/json'
     },
     body:JSON.stringify({
                           "word":wordPost,
                           "definition":definitionPost,
                           "sound":audioRecorded
                         })
   }).then((log) => alert(log))
   .then((res) => res.json())
   .then((data) => console.log(data))
   alert("succeed =) ! You have entered " + wordPost + " " + definitionPost +
    " " +audioRecorded  )
 }

render() {
    return (
      <form onSubmit={this.handleSubmit}>
            <label className="formLabel" htmlFor="word"> Enter a word </label>
            <input
              name="word"
              id="word"
              value={this.state.word}
              placeholder="enter a word"
              type="text"
              onChange={this.handleChange}
              />

            <label className="formLabel"
              htmlFor="definition"> Enter a definition
            </label>
            <textarea
              name="definition"
              value={this.state.definition}
              id="definition"
              cols="30"
              rows="10"
              onChange={this.handleChange}
              ></textarea>

            <label
              className="formLabel"
              htmlFor="audioRecord">
              Enter your audio record of the word
            </label>


            <AudioRecording name="audioRecorded"/>


            <input className="formLabel" id="submit" type="submit" name="Submit"/>
      </form>
    )
}
}
export default PostWord



